	module.exports = {"data":{"markdownRemark":{"html":"<p>ゲーム向け3DCGの指南書ってなかなかないもんだなぁ。単に3DCGの描き方とかはたくさんあるんだけど、ゲームで使用することを想定して書かれた物はあんまりないし、一方3Dプログラムの本もたくさんあるんだがモデリングまで触れているものとなるとほぼ皆無。</p>\n<ul>\n<li><a href=\"http://www.amazon.co.jp/%E3%82%B2%E3%83%BC%E3%83%A0CG%E6%95%99%E7%A7%91%E6%9B%B8-%E2%80%95%E3%82%AD%E3%83%A3%E3%83%A9%E3%82%AF%E3%82%BF%E3%83%BC%E3%83%BB%E8%83%8C%E6%99%AF%E7%B7%A8%E2%80%95-%E6%9D%89%E5%8E%9F-%E7%94%B1%E7%BE%8E%E5%AD%90/dp/4939007766/sr=8-1/qid=1169515055/ref=sr_1_1/249-5114787-5895507?ie=UTF8&#x26;s=books\">ゲームCG教科書</a>\n結局唯一あった1冊を購入したんだが、さらっと目を通した感じ結構一般的な3DCGの指南書って感じな気がする。アレか、あとは試行錯誤で極めろってか？まぁそれが本来あるべき姿だって言われたらぐうの音も返せないんだがｗ</li>\n</ul>","frontmatter":{"cover":null,"date":"2007-01-23T10:19:00+09:00","strDate":"2007/1/23","title":"3DCG描き方の本(WANTED)"}}},"pathContext":{"path":"/ja/blog/2007/01/23-3dcg描き方の本wanted/","langKey":"ja"}}

